import React, { useEffect, useState } from 'react'
import Sidebar from '../components/Sidebar';
import { useParams } from "react-router-dom";
import { SOCKET_URL, WEB_URL } from '../config';


function Chat() {
    const { thread_id } = useParams();
    const token = localStorage.getItem("token");

    const [message, setMessages] = useState([]);
    const [messageloading, setMessagesLoading] = useState(false);

    const socket = new WebSocket(`${SOCKET_URL}?token=${token}`);

    socket.onclose = () => {
        console.log('WebSocket connection closed');
    };

    socket.onmessage = (event) => {
        setMessagesLoading(true)
        console.log(event.data)
        setMessages(JSON.parse(event.data))
        setMessagesLoading(false)
    };

    socket.onerror = (error) => {
        console.error(error);
    };
    const checkConnection = async () => {
        socket.onopen = () => {
            console.log('WebSocket connection opened');
            fetchMessage();
        };
    }

    const fetchMessage = async () => {
        socket.send(
            JSON.stringify({ 'threadid': thread_id, "command": "fetch_messages" })
        );

    }

    useEffect(() => {
        checkConnection();
        // fetchMessage();
    }, [])

    const [new_message, setNewmessage] = useState('')
    const user_id = localStorage.getItem("user_id")

    const sendMessage = async (e) => {
        e.preventDefault();
        socket.send(
            JSON.stringify({
                'message': new_message,
                'threadid': thread_id,
                'userid': user_id,
                'messagetype': 'text',
                "command": "new_message"
            })
        );
    }

    const current_user = localStorage.getItem('thread_id')
    // const el = document.getElementById('messages')
    // el.scrollTop = el.scrollHeight
    return (
        <div>
            <Sidebar>
                <div className="flex-1 p:2 sm:p-6 justify-between flex flex-col h-screen">
                    <div id="messages" className="flex flex-col space-y-4 overflow-y-auto scrollbar-thumb-blue scrollbar-thumb-rounded scrollbar-track-blue-lighter scrollbar-w-2 scrolling-touch">
                        {messageloading ?
                            <></> :
                            <>
                                {message.map((e) => {
                                    return (
                                        <div>
                                            {e.thread_id == current_user ? <div className="chat-message">
                                                <div className="flex items-end justify-end">
                                                    <div className="flex flex-col space-y-2 text-xs max-w-xs mx-2 order-1 items-end">
                                                        <div>
                                                            <span className="px-4 py-2 rounded-lg inline-block rounded-br-none bg-black text-white ">
                                                                {e.messagetype == "text" ? <div>{e.message}</div> : <div>
                                                                    <img src={`${WEB_URL}${e.message}`} /></div>}
                                                            </span>
                                                        </div>
                                                        <div className='text-gray-500'>
                                                            {e.timestamp}
                                                        </div>
                                                    </div>
                                                    <img src="https://images.unsplash.com/photo-1590031905470-a1a1feacbb0b?ixlib=rb-1.2.1&amp;ixid=eyJhcHBfaWQiOjEyMDd9&amp;auto=format&amp;fit=facearea&amp;facepad=3&amp;w=144&amp;h=144" alt="My profile" className="w-6 h-6 rounded-full order-2" />
                                                </div>
                                            </div> :
                                                <div className="chat-message">
                                                    <div className="flex items-end">
                                                        <div className="flex flex-col space-y-2 text-xs max-w-xs mx-2 order-2 items-start">
                                                            <div>
                                                                <span className="px-4 py-2 rounded-lg inline-block rounded-br-none bg-blue-600 text-white ">
                                                                    {e.messagetype == "text" ? <div>{e.message}</div> : <div>
                                                                        <img src={`${WEB_URL}${e.message}`} /></div>}
                                                                </span>
                                                            </div>
                                                            <div className='text-gray-500'>
                                                                {e.timestamp}
                                                            </div>
                                                        </div>
                                                        <img src="https://images.unsplash.com/photo-1549078642-b2ba4bda0cdb?ixlib=rb-1.2.1&amp;ixid=eyJhcHBfaWQiOjEyMDd9&amp;auto=format&amp;fit=facearea&amp;facepad=3&amp;w=144&amp;h=144" alt="My profile" className="w-6 h-6 rounded-full order-1" />
                                                    </div>
                                                </div>}
                                        </div>
                                    )
                                })}
                            </>}
                    </div>
                    <div className="border-t-2 border-gray-200 pt-4 mb-2 sm:mb-0">
                        <div className="relative flex">
                            <input type="text" onChange={(e) => setNewmessage(e.target.value)} placeholder="Write your message!" className="w-full text-gray-600 placeholder-gray-600 pl-4 bg-gray-200 rounded-md py-3" />
                            <div className="absolute right-0 items-center inset-y-0 hidden sm:flex">
                                <button onClick={sendMessage} type="button" className="inline-flex items-center justify-center rounded-lg px-4 py-3 transition duration-500 ease-in-out text-white bg-black">
                                    <span className="font-bold">Send</span>
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" className="h-6 w-6 ml-2 transform rotate-90">
                                        <path d="M10.894 2.553a1 1 0 00-1.788 0l-7 14a1 1 0 001.169 1.409l5-1.429A1 1 0 009 15.571V11a1 1 0 112 0v4.571a1 1 0 00.725.962l5 1.428a1 1 0 001.17-1.408l-7-14z"></path>
                                    </svg>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </Sidebar>
        </div>
    )
}

export default Chat


