import Topnav from './Topnav';
import { Link } from 'react-router-dom'
import React, { useState, useEffect } from 'react'
import { motion } from "framer-motion";
import { fetchUserServices } from '../services/Services';
import { API_URL, WEB_URL } from '../config';



function Sidebar({ children }) {
  const [isOpen, setIsOpen] = useState(false)
  const [users, setUsers] = useState([]);

  const [loading, setLoading] = useState(false)
  const getUsers = async () => {
    setLoading(true);
    const res = await fetchUserServices();
    setUsers(res);
    setLoading(false);
  }
  useEffect(() => {
    getUsers();
  }, [])

  return (
    <motion.div
      animate={"enter"}>
      <div
        className={`min-h-screen  flex flex-col flex-auto flex-shrink-0 antialiased bg-gray-100`}
      >
        <Topnav />
        <div id="sidebar" className={'bg-white dark:bg-black hidden md:fixed md:flex md:flex-col w-64 md:h-full'}>
          <div className={'overflow-y-auto overflow-x-hidden flex-grow'}>
            <ul className={'flex flex-col'}>
              <li>
                <div className={'px-2 mt-24'}>
                  <span className={'text-bold text-lg pb-8'}>All Messages</span>
                  <div class="mx-auto text-gray-600 pb-2">
                    <input class="w-full border-2 border-gray-300 bg-white h-10 px-3 rounded-lg text-sm"
                      type="search" name="search" placeholder="Search" />
                  </div>
                  <span className={'text-md tracking-wide'}>
                    {loading ? <></> : <>{users.map((e) => {
                      return (
                        <div>

                          {e.friends.map((i) => (
                            <div className='border-b'>
                              <Link
                                to={`/${i.thread}`}
                                className={
                                  `bg-white transition border-2 border-white hover:shadow capitalize duration-400 ease-in text-black py-2`
                                }
                              >
                                <div>
                                  <div className="flex justify-between items-center px-2">
                                    <div className="text-gray-600">
                                      {i.username}
                                    </div >
                                    <div className="text-gray-600 text-xs font-bold">
                                      Offline
                                    </div>
                                  </div>
                                  <img src={`${API_URL}/media/${i.userpicture}`} className="w-12"/>
                                  <div className="flex justify-between items-center px-2">
                                    <div className="text-gray-600">
                                      How are you
                                    </div >
                                    <div className="text-gray-600 text-xs font-bold">
                                      15 min ago.
                                    </div>
                                  </div>
                                </div>
                              </Link>
                            </div>
                          ))}
                        </div>
                      )
                    })}</>}
                  </span>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div className={'m-1 md:pl-64'} id="mainContent">
          <div className={'px-5 pt-24'}>{children}</div>
        </div>
      </div>
    </motion.div >
  )
}
export default Sidebar


