import { API_URL } from '../config';
import axios from 'axios'

export const userLogin = async ({ phone }) => {

    const apiRes = await axios.get(`${API_URL}/verify/${phone}`)
    console.log(apiRes)
    if (apiRes.status == 200) {
        return true;
    } else
        return false;

};

export const userVerify = async ({ phone,otp }) => {
    const data = {
        "otp":otp
    }

    const apiRes = await axios.post(`${API_URL}/verify/${phone}/`, data, {
        headers: {
            'Content-Type': 'multipart/form-data'
        },
    })
    console.log(apiRes)
    if (apiRes.status == 200) {
        localStorage.setItem("token", apiRes.data.token)
        localStorage.setItem("user_id", apiRes.data.user_id)
        return true;
    } else
        return false;

};


export const fetchUserServices = async () => {
    const Token = localStorage.getItem('token');
    const apiRes = await axios.get(`${API_URL}/chat/get/friends/`,{
        headers: {
            'Authorization': `Token ${Token}`,
            'Content-Type': 'multipart/form-data'
        },
    })
    if (apiRes.status == 200) {
        return apiRes.data;
    } else
        return false;

};
