import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { userLogin, userVerify } from '../services/Services'
import { useDispatch } from 'react-redux'
import { login } from '../feature/userSlice'

function Login() {
    const history = useNavigate()
    const [phone, setPhone] = useState('')
    const [otp, setOTP] = useState('')
    const [loading, setLoading] = useState(false);

    const [otpenable, setOTPEnable] = useState(false);
    const dispatch = useDispatch()
    const handleSubmit = async (e) => {
        e.preventDefault()
        setLoading(true)
        const res = await userLogin({ phone })
        if (res === true) {
            setOTPEnable(true)
        } else {
            setOTPEnable(false)
        }
        setLoading(false)
    }

    const handleVerify = async (e) => {
        e.preventDefault()
        setLoading(true)
        const res = await userVerify({ phone,otp })
        if (res === true) {
            dispatch(login())
            history('/')
        } else {
        }
        setLoading(false)
    }
    return (
        <>
            <div className="p-5 bg-gray-100">
                <div className="md:flex items-center h-screen">
                    <div className="md:w-1/3"></div>
                    <div className="md:w-1/3 bg-white p-4 rounded-lg">
                        <div className={`custom-font text-3xl font-bold text-center`}>
                            login
                        </div>
                        {otpenable ?
                            <form onSubmit={(e) => handleVerify(e)}>
                                <div className="mb-4">
                                    <label className="block text-grey-darker text-sm font-bold mb-2" for="phone">
                                        Phone
                                    </label>
                                    <input value={phone} className="border rounded-lg w-full py-2 px-3 text-grey-darker" id="phone" type="text" placeholder="Phone Number" />
                                </div>
                                <div className="mb-4">
                                    <label className="block text-grey-darker text-sm font-bold mb-2" for="otp">
                                        One Time Passcode
                                    </label>
                                    <input value={otp} onChange={(e) => setOTP(e.target.value)} className="border rounded-lg w-full py-2 px-3 text-grey-darker" id="otp" type="text" placeholder="One Time Passcode" />
                                </div>
                                <div className='h-5'></div>
                                <button type='submit' className='py-2 px-4 bg-black rounded-lg text-white'>Login</button>
                            </form>
                            : <form onSubmit={(e) => handleSubmit(e)}>
                                <div className="mb-4">
                                    <label className="block text-grey-darker text-sm font-bold mb-2" for="phone">
                                        Phone
                                    </label>
                                    <input value={phone}
                                    onChange={(e) => setPhone(e.target.value)} className="border rounded-lg w-full py-2 px-3 text-grey-darker" id="phone" type="text" placeholder="Phone Number" />
                                </div>
                                <button type='submit' className='py-2 px-4 bg-black rounded-lg text-white'>Login</button>
                            </form>}
                    </div>
                    <div className="md:w-1/3"></div>
                </div>
            </div>
        </>
    )
}

export default Login