import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { logout } from '../feature/userSlice';


function Topnav() {
  const [isOpen, setIsOpen] = useState(false)
  useEffect(() => {
  }, [])

  function sidebarToggle() {
    var sidebar = document.getElementById('sidebar')
    var main = document.getElementById('mainContent')
    if (sidebar.classList.contains('md:fixed')) {
      sidebar.classList.remove('md:fixed');
      sidebar.classList.remove('md:hidden');
      sidebar.classList.remove('md:flex');
      sidebar.classList.remove('w-64');
      sidebar.classList.add('transition');
      sidebar.classList.add('ease-out');
      sidebar.classList.add('duration-400');
      main.classList.remove('md:pl-64');
    } else {
      sidebar.classList.add('md:fixed');
      sidebar.classList.add('md:flex');
      sidebar.classList.add('md:w-64');
      sidebar.classList.add('transition');
      sidebar.classList.add('ease-in');
      sidebar.classList.add('duration-400');
      main.classList.add('md:pl-64');
    }
  }

  function mobilesidebarToggle() {
  }

  const dispatch = useDispatch()

  const auth = useSelector((state) => state.user.isLoggedIn)
  const logoutHandler = () => {
    dispatch(logout())
  }

  return (
    <div>
      <div className={`bg-black w-full flex items-center justify-end py-5 px-8 space-x-6 fixed z-50`}>
        <a
          href="/"
          className="text-white dark:text-white font-bold mr-auto text-xl md:px-14 my-font uppercase"
        >
          Opt chat
        </a>
        <div className="hidden md:block text-white dark:text-white">
          <button
            onClick={logoutHandler}
            className="px-3 py-2 transition text-sm font-medium tracking-wide duration-400 ease-in"
          >
            Logout
          </button>
        </div>
        <button
          onClick={() => sidebarToggle()}
          type="button"
          className="hidden md:block items-center justify-center text-white dark:text-white transition duration-400 ease-in"
          aria-expanded="false"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth="2"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M4 6h16M4 12h8m-8 6h16"
            />
          </svg>
        </button>
        <button
          onClick={() => mobilesidebarToggle()}
          type="button"
          className="block md:hidden items-center justify-center text-white dark:text-white transition duration-400 ease-in"
          aria-expanded="false"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth="2"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M4 6h16M4 12h8m-8 6h16"
            />
          </svg>
        </button>
      </div>
    </div>
  )
}

export default Topnav